* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.article{
    clear: both;
    width: 50%;
    margin-left: 25%;
}

body {
    background-color: lightgrey;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
    font-size: 14px;
    line-height: 1.5;
}

.img-rounded{
    border-radius:50%;
    width: 12rem;
    border: white solid 0.125rem;
}

.profile{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    float: left;
    margin: 1rem 8rem;
}
ul.navbar{
    float: right;
    margin-right: 10rem;
}

ul.navbar li{
    background: #add8e630;
    border-radius: 0.2rem;
    border: 0.1rem white solid;
    padding: 1rem;
    display: inline;
    margin: 1rem;
    cursor: pointer;
}

.thumbs-up{background-image: url("data:image/svg+xml;base64,")}

.thumbs-down{background-image: url("data:image/svg+xml;base64,")}


